import React, {PropTypes} from 'react';
import moment from 'moment';
import { Form, Input, Button, Row, Col, DatePicker, Select } from 'antd';
import {costType} from '@/common/constants.js'

const Option = Select.Option;
const FormItem = Form.Item;
const RangePicker = DatePicker.RangePicker;


class SearchForm extends React.Component {
  constructor() {
    super();
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
        this.props.onSearch.call(this, values);
      }
    });
  }
  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: { span: 6 },
      wrapperCol: { span: 18 },
    };
    const rangeConfig = {
      initialValue: [moment(new Date()).subtract(30, 'days'), moment(new Date())],
      rules: [{ type: 'array', required: true, message: 'Please select time!' }],
    };
    return (
      <div>
        <Form >
          <Row>
          <Col span={8}>
              <FormItem
              {...formItemLayout}
              label=""
            >
              {getFieldDecorator('range-picker', rangeConfig)(
                <RangePicker />
              )}
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem
            {...formItemLayout}
            label=""
            hasFeedback
          >
            {getFieldDecorator('itemType', {
              rules: [{
                required: true, message: '',
              }],
            })(
              <Select>
                {Object.keys(costType).map(val=>(<Option key={val} value={val}>{costType[val]}</Option>))}
              </Select>
            )}
          </FormItem>
          </Col>
          <Col span={8}>
            <FormItem
              label="dddd"
              {...formItemLayout}
            >
              <Input placeholder="input placeholder" />
            </FormItem>
          </Col>
          </Row>
          <Row>
            <Col  offset={18} span={8}>
              <Button size="large" type="primary" onClick={this.handleSubmit.bind(this)} style={{marginRight: '15px'}}>Search</Button>
              <Button size="large" type="ghost">Reset</Button>
            </Col>
          </Row>
        </Form>
      </div>
    );
  }
}
SearchForm.propTypes = {
  form: PropTypes.object,
}
export default Form.create()(SearchForm);



