import React from 'react';
import { Table, message } from 'antd';
import ReactMixin from "react-mixin"
import TableAction from '@/common/mixin/TableAction.jsx';
import {costType} from '@/common/constants.js'
import axios from 'axios';
import moment from 'moment';
const columns = [{
  title: '',
  dataIndex: 'expensesId',
}, {
  title: '',
  dataIndex: 'itemType',
  render:(text)=>{
    return costType[text];
  },
}, {
  title: '',
  dataIndex: 'itemValue',
  render:(val)=>{
    return `${val}`;
  },
}, {
  title: '',
  dataIndex: 'itemDescription',
}, {
  title: '',
  dataIndex: 'createTime',
  render:(val)=>{
    return moment(val).format('YYYY MMMM Do, h:mm:ss');
  },
}, {
  title: '',
  dataIndex: 'bbb',
  render: () => {
    return (<div>
      <a></a>
      <span>|</span>
      <a></a>
    </div>);
  },
}];
@ReactMixin.decorate(TableAction)
class SearchTable extends React.Component {
  constructor() {
    super();
    this.state = {
      loading: false,
      data: [],
      pagination: {
        showSizeChanger: true,
        current: 1,
        pageSize: 10,
        showTotal: total=>`${total}`,
      },
    };
  }
  fetch(param) {
    axios.get('/expenses/list', { params: param })
    .then( (response)=> {
      const responseData =  response.data || {};
      console.log(response);
      console.log(responseData);
      if (responseData.state = "success") {
        const data = responseData.data;
        const pagination = {...this.state.pagination};
        pagination.current = responseData.pageNumber;
        pagination.pageSize = responseData.pageSize;
        pagination.total = responseData.totalElements;
        this.setState({ 
          pagination,
          data,
        });
      }
    })
    .catch(function (error) {
      message.error(error);
      console.log(error);
      this.setState({ btnLoading: false });
    });
  }
  render() {
    const { pagination, data } = this.state;
    return (
      <div>
        <Table columns={columns} onChange={this.onTableChange.bind(this)} dataSource={data} pagination={pagination} />
      </div>
    );
  }
}

export default SearchTable;



